html{
  width: 100%;
  overflow-x: hidden;
}
*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    background-color: hsl(0, 0%, 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
              /*HEADER*/

#bnb-header{
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  height: 11vh;
}
#bnb-logo{
  padding: 15px;
  position: relative;
  width: 10vw;
  left: 50px;
}

                 /*HERO SECTION*/
#hero{
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 20vw;
}
#Title{
column-count: 1;
}
.act-img{
  height: 10vh;
}

#imageholder {
  display: grid;
  grid-template-columns: 12vh 12vh 12vh 12vh 12vh;
  grid-template-rows: 73px 105px 105px;
  gap: 0px px;
  grid-auto-flow: row;
  grid-template-areas:
      ". img2 . img6 ."
      "img1 img3 img4 img7 img8"
      ". . img5 . img9";
  align-items: end;
  justify-content: center;
  position: relative;
  top: 15vh;
  left: 10vw;

}

#Title{
  position: relative;
  top: 10vh;
  left: 3vw;
}

#Title > h1 {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  font-size: 7vh;
  color: #f15d62;
}
#Title > p {
  width: 40vw;
  color :gray;
} 

#img1 { grid-area: img1;}

#img2 { grid-area: img2;}

#img3 { grid-area: img3;}

#img4 { grid-area: img4;}

#img5 { grid-area: img5;}

#img6 { grid-area: img6;}

#img7 { grid-area: img7;}

#img8 { grid-area: img8;}

#img9 { grid-area: img9;}

#deco{
  height: 30vh;
  width: 45vw;
  border-radius: 2rem;
  background-color: whitesmoke;
  position: absolute;
  top: 25vh;
  left: -1vw;
}
#top-btn1{
  padding: 15px;
  border-radius: 0.5rem;
  background-color: #f15d62;
  color: white;
  font-weight:600 ;
  border: none;
  margin-top: 3vh;
}
#top-btn2{
  top: 5px;
  padding: 14px;
  border-radius: 0.5rem;
  margin-left: 3vw;
  background-color: white;
  color: #f15d62;
  border: #f15d62 1px solid;
}
                    /*CARD SECTION*/

.bold {
  font-weight: bold;
}
.gray {
  color: #918E9B;
}
.card {
  min-width: 21vw;
  height: 55vh;
  font-size: 2.3vh;
  padding: 40px;
  
}

.card--image {
  width: 100%;
  height: 100%;
  border-radius: 9px;
}

.card--stats {
  display: flex;
  align-items: center;
}

.card--star {
  height: 14px;
}
.card-sales{
  position: relative;
  top: 20px;
}

.inner-carousel{
  display: flex;
  cursor: grab;
}

.inner-carousel2{
  cursor: grab;
  display: none;
}


.carousel{
  overflow: hidden;
  height: 65vh;
  margin-left: 15vh;
}
#cards{
  position: relative;
  top: 45vh;
  align-items: center;
  margin-bottom: 30vh;
}
#CARDS-TITLE{
  position: relative;
  top: 43vh;
  left: 5vw;
}
#image-cont{
  display: grid;
  grid-template-columns: auto auto;
  justify-content: center;
  align-items: center;
  margin-top: 40vh;
  column-gap: 2.5vw;
}
                          /*activities*/

  .activity-sec{
    min-height: 120vh;
  }
  .activity{      
  height: 90vh;
  width: 43vw;
  border-radius: 2rem;
}
#EXPERIENCE-TITLE{
  position: relative;
  top: 20vh;
  text-align: center;
}
.ACT-TXT{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 5.2vh;
  font-weight: 800;
  color: white;
  position: relative;
  top: -30vh;
  left: 3vw;
}
.ACT-BTN
{
  color: black;
  font-family: sans-serif;
  font-size: 2.2vh;
  padding: 15px;
  background-color: white;
  font-weight: 500;
  border: none;
  border-radius: 0.5rem;
}
                        /*banner*/
#ban{
  margin-top: 60vh;
  margin: auto;
  padding-left: 4.7%;
  height: 110vh;
}

#ban2{
  display: none;
}
#ban-img{
  width: 90vw;
  border-radius: 2rem;

}

.BAN-BTN{
  color: black;
  font-family: sans-serif;
  font-size: 2.2vh;
  padding: 15px;
  background-color: white;
  font-weight: 500;
  border: none;
  border-radius: 0.5rem;
  margin-top: 30vh;
}
.BAN-TXT{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 10vh;
  font-weight: 800;
  color: white;
  position: relative;
  top: -90vh;
  left: 5vw;
}
                             /*FOOTER*/
#footer-cont{
  background-color: whitesmoke;
  width: 100%;
}
#upper-footer{
  display: grid;
  grid-template-columns: auto auto auto auto;
  align-content: space-around;
    justify-items: center;
    padding-bottom: 5vh;
    padding: 15px;
}
.footer-txt{
  color: #918E9B;
  padding: 3px;
}
hr{
  width: 80%;
  color: #918E9B;
}
#lower-footer{
  height: 5vh;
  display: grid;
  grid-template-columns: auto auto;
    justify-content: space-around;
}

